    }
}
@keyframes ptanimte {
    0%{
        opacity: 1;
        right: -20px;
        opacity: 0;
    }
    50%{
        opacity: 1;

        right: 50%;
        transform: rotate(500deg);
    }
    100%{
        right: 100%;
    }
}
