            img:nth-of-type(3){
                animation:  eight-3 2s 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards ;
            }
            
        }

    }
}
@keyframes eight-2{
    0%{
        transform: translate(-36px, 9px);
        opacity: 0;    
    }
    100%{
        transform: translate(0, 0);
        opacity: 1;
    
    }
}
@keyframes eight-3{
    0%{
        transform: translateX(100px);
        opacity: 0;
    }
    100%{
        transform: translateX(0);
        opacity: 1;
    
    }
}
@keyframes eight-4{
    0%{
        transform: rotate(-6deg);
    }
    100%{
        transform: rotate(6deg);
    
    }
}
@keyframes nine {
    0%{
        transform: scale3d(1, 1, 1);
    }
    100%{
        transform: scale3d(1.2, 1.2, 1.2);
    }
}
